import { all, takeEvery, put, call, fork } from 'redux-saga/effects';
//import { push } from 'react-router-redux';
import SuperFetch from '../../helpers/superFetch';
import notification from '../../components/notification';
import actions from './actions';

export function* addNewClaim() {
  yield takeEvery('INSERTCLAIM_REQUEST', function*({ payload }) {
    const result = yield call(
      async payload => {

        return await SuperFetch.post('claim/insertClaim', payload).then(response => {

          return response.data.claimID;
        });
      },
    payload);
    if(result) {
      yield put({
        type: actions.CREATECLAIM_SUCCESS,
        payload: result
      });
    } else {
      notification('error', "something Wrong" || result);
    }

  });
}

