export function* addNewClaimSuccess() {
  yield takeEvery(actions.CREATECLAIM_SUCCESS, function*({ payload }) {
    //console.log("CREATECLAIM_SUCCESS",payload);
    //yield put(push('/dashboard/claimDetail/'+payload));
  });
}

export default function* rootSaga() {
  yield all([
    fork(addNewClaim),
    fork(addNewClaimSuccess)
  ]);
}
