const actions = {
  CREATECLAIM_REQUEST: 'INSERTCLAIM_REQUEST',
  CREATECLAIM_SUCCESS: 'CREATECLAIM_SUCCESS',
  RESETCLAIM: 'RESETCLAIM',
  addNewClaim: (payload) => {
    return (dispatch, getState) => {
      dispatch({
        type: actions.CREATECLAIM_REQUEST,
        payload
      });
    };
  },
  resetClaim: () => {
    return (dispatch, getState) => {
      dispatch({
        type: actions.RESETCLAIM
      });
    };
  }
};
export default actions;
